/* Color Palette and Typography: Start */
/* End: Color Palette and Typography */
.sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  color: #000000;
}
.sidebar .sidebar-bg {
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 400px;
  background-color: #ffffff;
}
.sidebar .sidebar-bg .links {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.sidebar .sidebar-bg .links a {
  font-size: 40px;
}
.sidebar button {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: fixed;
  top: 25px;
  left: 25px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 999;
}/*# sourceMappingURL=Sidebar.css.map */