/* Color Palette and Typography: Start */
$sidebar-base-color: #ffffff;
$sidebar-text-color: #000000;
$sidebar-font-size: 40px;
/* End: Color Palette and Typography */

.sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: $sidebar-base-color;
  color: $sidebar-text-color;

  .sidebar-bg {
    z-index: 999;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 400px;
    background-color: $sidebar-base-color;

    .links {
      position: absolute;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 20px;

      a {
        font-size: $sidebar-font-size;
      }
    }
  }
  button {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: fixed;
    top: 25px;
    left: 25px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    z-index: 999;
  }
}
